import {createRouter, createWebHashHistory} from 'vue-router'
// import store from "../store/index"

const router =  createRouter({
    // mode:"hash",
    "history": createWebHashHistory(process.env.BASE_URL),
    // "history": createWebHistory(),
    publicPath: "./",
    routes: [
        {
            name: "homepage",
            path: "/",
            component: ()=>import("@/components/HomeComponent"),
        },{
            name: "TrainComponent",
            path: "/TrainComponent",
            component: ()=>import("@/components/TrainComponent.vue"),
        },{
            name:"TrainController",
            path:"/TrainController",
            component:()=>import("@/page/train_pages/TrainParamController.vue"),
        },{
            name:"PredictController",
            path:"/PredictController",
            component:()=>import("@/components/PredictComponent.vue"),
        }

    ],
})

export default router
