import {createStore} from 'vuex'
import {ElNotification} from "element-plus";

// var pyqt5 = window.pyqt5

export default createStore({

    state() {
        return {
            currentPage:0,
            modelInfoList: {},
            currentShow: {},
            drawer: false,
            filename: ""
        }

    },
    actions: {},
    mutations: {
        // 获取后台数据
        flushModelInfoList(state) {
            try {
                window.pyqt5.data.get_model_info().then((data) => {
                    {
                        let newModelInfoList = JSON.parse(data)


                        state.modelInfoList['E'] = newModelInfoList['E']
                        state.modelInfoList['IC'] = newModelInfoList['IC']
                        state.modelInfoList['GPCR'] = newModelInfoList['GPCR']
                        state.modelInfoList['NR'] = newModelInfoList['NR']
                    }
                })
            } catch (e) {
                console.log(e)
            }
        },

        // 获取模型训练数据
        getInfo() {
            window.pyqt5.data.get_info().then((data) => {
                let info = JSON.parse(data)
                if (info.state !== -1) {

                    if (info.state === 0) {
                        ElNotification({
                            title: info.title,
                            message: info.message,
                            duration: 0,
                            type: 'success'
                        })
                    } else {
                        ElNotification({
                            title: info.title,
                            message: info.message,
                            duration: 0,
                            type: 'error'
                        })
                    }
                }

            })
        },

        /*模型创建*/
        createModel(state, params) {

            window.pyqt5.train.model_train(JSON.stringify(params)).then((data) => {
                if (data) {
                    ElNotification({
                        title: '模型训练',
                        message: '模型已经开始预测请耐心等待',
                        duration: 0,
                    })
                }
            });
        },

        // 获取预测数据文件
        getFileName(state) {
            window.pyqt5.predict.getFileName().then((data) => {
                state.filename = data
            })
        },

        // 当前模型预览数据
        setCurrentShow(state, data) {
            state.currentShow = data
            state.drawer = !state.drawer
        },

        // 数据展示显示
        setDrawer(state, value) {
            state.drawer = value
        },

        setCurrentPage(state,value){
            state.currentPage = value
        }

    },
    getters: {},
    modules: {}
})
